import React from "react";
import "./ShareRegistrySkeleton.scss";
import RegistryImageHeaderSkeleton from "../registry-image-header-skeleton/RegistryImageHeaderSkeleton";
import GiftCardSkeleton from "../gift-card-skeleton/GiftCardSkeleton";
import {ROUTES} from "../../../../../utils/cdn-assets-links";

const ShareRegistrySkeleton = ({registryInfo, elementId, height, width}) => {
  const nubmerOfGifts = 6;

  let gifts = []
  for (let i = 0; i < nubmerOfGifts; i++) {
    gifts.push(
      <GiftCardSkeleton/>
    )
  }
  return (
    <div
      id={elementId}
      className="share-registry-skeleton-container px-6 pt-6"
      style={{
        height: height,
        width: width
      }}
    >
      <div className="share-registry-skeleton-main-content">
        <div className="w-100 share-registry-ma-logo-section py-3">
          <img className="d-block mx-auto" id="skeleton-ma-logo" src="https://baby-registry-dev.s3.us-east-2.amazonaws.com/media/frontend_assets/MA-Logo1.5x.svg" alt="logo" crossOrigin="anonymous"/>
        </div>
        <RegistryImageHeaderSkeleton headerImage={registryInfo.image.image} registryName={registryInfo.displayName}/>
        <div className="container">
          <div className="row mt-5 justify-content-center">
            <div className="col-9">
              {gifts.map(gift => (
                <div className="row">
                  <div className="col">
                    {gift}
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default ShareRegistrySkeleton;