import React from "react";
import "./BackToTop.scss";

const BackToTop = () => {

  const gotoToTop = () => {
    window.scrollTo(0,0);
  }

  return (
    <div className="ma-back-to-top-btn p-2" id="ma-back-to-top" onClick={gotoToTop}><i className="ma-back-to-top-icon icon-chevron-up"/></div>
  )
}

export default BackToTop;