import React from "react";
import Button from "../button-component/ButtonComponent";
import Modal from "../modal-component/ModalComponent";

const ConfirmUrlChangeModal = ({show, closeModalHandler, confirmClickHandler}) => {
  return (
    <Modal
      show={show}
      size="md"
      closeIconClass="mb-auto"
      noFullSizeMobile={true}
      footer={(
        <>
          <Button variant="secondary" className="px-6 py-1" onClick={closeModalHandler}>No,
            cancel</Button>
          <Button variant="primary" className="px-6 py-1" onClick={confirmClickHandler}>Yes, change</Button>
        </>
      )}
      closeModal={closeModalHandler}>
      You may lose the items on this screen. Are you sure you want to change?
    </Modal>
  )
}

export default ConfirmUrlChangeModal;