import React, {useState, useEffect, useRef, useCallback} from "react";
import ReactCrop from "react-image-crop";
import "react-image-crop/lib/ReactCrop.scss";

const CropImage = (props) => {
  // DEFAULT CONSTANTS
  const ASPECT_RATIO = 16/3
  const CROPPED_IMAGE_FORMAT = "image/jpeg";
  const COVER_IMAGE_NAME = "cover.jpeg";

  // VARIABLE INITIALIZATION
  const imgRef = useRef(null);
  const [crop, setCrop] = useState({});
  const [completedCrop, setCompletedCrop] = useState(null);

  // SET IMAGE TO LOADED IMAGE
  const onLoad = useCallback((img) => {
    imgRef.current = img;
    const aspect = ASPECT_RATIO;
    const width = (img.width / aspect < img.height * aspect ? 100 : ((img.height * aspect) / img.width) * 100)*0.85;
    const height = (img.width / aspect > img.height * aspect ? 100 : (img.width / aspect / img.height) * 100)*0.85;
    const y = (100 - height) / 2;
    const x = (100 - width) / 2;

    setCrop({
      unit: '%',
      width:width,
      height:height,
      x: x,
      y:y,
      aspect: aspect
    });
    return false
  }, []);

  // RETURN BLOB OF CROPPED IMAGE
  const getCroppedImg = (image, crop, fileName) => {
    const canvas = document.createElement("canvas");
    const scaleX = image.naturalWidth / image.width;
    const scaleY = image.naturalHeight / image.height;
    canvas.width = crop.width;
    canvas.height = crop.height;
    const ctx = canvas.getContext("2d");

    ctx.drawImage(
      image,
      crop.x * scaleX,
      crop.y * scaleY,
      crop.width * scaleX,
      crop.height * scaleY,
      0,
      0,
      crop.width,
      crop.height
    );

    return new Promise((resolve, reject) => {
      canvas.toBlob(
        (blob) => {
          if (blob) {
            blob.name = fileName;
            resolve(blob);
          } else {
            reject("Blob is null");
          }
        },
        CROPPED_IMAGE_FORMAT,
        1
      );
    });
  };

  // SET CROPPED IMAGE TO PARENT STATE
  useEffect(() => {
    if (!completedCrop || !imgRef.current) {
      return;
    }
    let croppedImage = getCroppedImg(
      imgRef.current,
      completedCrop,
      COVER_IMAGE_NAME
    );
    croppedImage
      .then((blob) => {
        props.setCroppedImage(blob);
      })
      .catch((_) => {
      });
  }, [completedCrop]);

  return (
    <>
      <ReactCrop
        src={props.src}
        crop={crop}
        crossorigin="anonymous"
        // locked={true}
        style={{backgroundColor: "white"}}
        onImageLoaded={onLoad}
        keepSelection={true}
        onChange={(newCrop) => setCrop(newCrop)}
        onComplete={(c) => setCompletedCrop(c)}
      />
    </>
  );
};

export default CropImage;
