import React from "react";
import {graphQLNoToken} from "../../../../api/axios";
import {VERIFY_CANCEL_RESERVATION} from "../../../../api/queries";
import Modal from "../modal-component/ModalComponent";
import {toast} from "react-toastify";
import Button from "../button-component/ButtonComponent";
import {withRouter} from "react-router";
import Counter from "../counter/Counter";
import {CANCEL_RESERVATION_VIA_LINK} from "../../../../api/mutations";
import {setLoader} from "../../../registry/RegistryActions";
import {VIEW_GIFT_PRODUCTS_PER_REQUEST, LOCAL_STORAGE_GUEST_GIFT_BUY_KEY} from "../../../../utils/constants";

class CancelReservation extends React.Component {
  state = {
    cancelReservationItem: null,
    showCancelReservation: false,
    quantityToCancel: 1
  }

  closeModalHandler = () => {
    let queryParams = new URLSearchParams(window.location.search)
    if (queryParams.has('cr')) {
      queryParams.delete('cr')
      this.props.history.replace({
        search: queryParams.toString(),
      })
    }
    this.setState({
      showCancelReservation: false
    })
  }

  componentDidMount() {
    if (this.props.registryInfo?.info) {
      let queryParams = new URLSearchParams(decodeURI(window.location.search))
      if (queryParams.has('cr')) {
        let tempToken = queryParams.get('cr').replaceAll(" ", "+")
        const token = tempToken.slice(2, tempToken.length - 1)
        let reqData = {
          query: VERIFY_CANCEL_RESERVATION,
          variables: {
            token: token
          }
        }
        graphQLNoToken(reqData).then(res => {
          if (res?.data?.data) {
            let localCancelReservationItem = res.data.data.verifyCancelReservationToken

            console.log(this.props.registryInfo, localCancelReservationItem)
            if (this.props.registryInfo &&
              Number(this.props.registryInfo?.info.id) === Number(localCancelReservationItem.purchasedRegistryItem.registryItem.registry.id) &&
              localCancelReservationItem.maxAllowedQuantity > 0) {

              this.setState({
                cancelReservationItem: localCancelReservationItem,
                showCancelReservation: true
              })
            }
          }
        }).catch(err => {
          if (err?.response?.data?.errors) {
            for (let error of err.response.data.errors) {
              toast.error(error.message)
            }
          }
        })
      }
    }

  }

  incrementQuantityHandler = () => {
    if (this.state.quantityToCancel + 1 <= this.state.cancelReservationItem.maxAllowedQuantity) {
      this.setState({
        quantityToCancel: this.state.quantityToCancel + 1
      })
    }
  }

  decrementQuantityHandler = () => {
    if (this.state.quantityToCancel - 1 > 0) {
      this.setState({
        quantityToCancel: this.state.quantityToCancel - 1
      })
    }
  }

  cancelReservationClickHandler = () => {
    let reqData = {
      query: CANCEL_RESERVATION_VIA_LINK,
      variables: {
        registryId: Number(this.state.cancelReservationItem.purchasedRegistryItem.registryItem.registry.id),
        registryItemId: Number(this.state.cancelReservationItem.purchasedRegistryItem.registryItem.id),
        email: this.state.cancelReservationItem.purchasedRegistryItem.guestUser.email,
        removeReservedQuantity: this.state.quantityToCancel,
        keepReservedQuantity: Number(this.state.cancelReservationItem.maxAllowedQuantity) - this.state.quantityToCancel
      }
    }
    setLoader(true)
    graphQLNoToken(reqData).then(res => {
      if (res?.data?.data?.removeReservedRegistryItem?.message) {
        toast.success(res.data.data.removeReservedRegistryItem.message)
        this.removeProductFromList()
        this.closeModalHandler()
        if (this.state.cancelReservationItem &&
          this.state.cancelReservationItem?.purchasedRegistryItem?.registryItem?.registry?.isPublic &&
          this.props.updateGuestViewProduct
        ) {
          this.props.updateGuestViewProduct(this.props.urlSlug, VIEW_GIFT_PRODUCTS_PER_REQUEST, true)
        }
      }
    }).catch(err => {
      if (err?.response?.data?.errors) {
        for (let error of err.response.data.errors) {
          toast.error(`Cancel Reservation: ${error.message}`)
        }
      }
    }).finally(() => {
      setLoader(false)
    })
  }

  removeProductFromList = () => {
    let jsonVisitedItems = JSON.parse(localStorage.getItem(LOCAL_STORAGE_GUEST_GIFT_BUY_KEY))
    if (jsonVisitedItems) {
      let updatedProducts = []
      let itemId = this.state.cancelReservationItem.purchasedRegistryItem.registryItem.id
      for (let product of jsonVisitedItems) {
        if (product.pId === itemId) {
          let finalProduct = {...product}
          finalProduct.quantity = Number(this.state.cancelReservationItem.maxAllowedQuantity) - this.state.quantityToCancel
          if (finalProduct.quantity > 0) {
            updatedProducts.push(finalProduct)
          }
        } else {
          updatedProducts.push(product)
        }
      }
      // let updatedProducts = jsonVisitedItems.filter(product => product.pId !== this.state.cancelReservationItem.purchasedRegistryItem.registryItem.id)
      localStorage.setItem(LOCAL_STORAGE_GUEST_GIFT_BUY_KEY, JSON.stringify(updatedProducts))
    }
  }

  render() {
    let modalBody = null;
    let modalFooter = (
      <>
        <Button variant="secondary" className="font-medium px-2 py-1" onClick={this.closeModalHandler}>Keep the
          reservation</Button>
        <Button variant="primary" className="font-medium px-2 py-1" onClick={this.cancelReservationClickHandler}>Cancel
          Reservation</Button>
      </>
    )

    if (this.state.cancelReservationItem?.maxAllowedQuantity === 1) {
      modalBody = (
        <p>Are you sure you want to cancel reservation
          of <b>{this.state.cancelReservationItem.purchasedRegistryItem.registryItem.title}</b> ?</p>
      )
    } else if (this.state.cancelReservationItem?.maxAllowedQuantity > 1) {
      modalBody = (
        <div>
          <p>Are you sure you want to cancel reservation
            of <b>{this.state.cancelReservationItem.purchasedRegistryItem.registryItem.title}</b> ?</p>
          <p>You reserved {this.state.cancelReservationItem.maxAllowedQuantity} qty. How many do you want to cancel?</p>

          <Counter
            type="square"
            className="my-4"
            incrementHandler={this.incrementQuantityHandler}
            decrementHandler={this.decrementQuantityHandler}
            counter={this.state.quantityToCancel}
          />
        </div>
      )
    }

    return (
      <Modal
        size='md'
        show={this.state.showCancelReservation}
        footer={modalFooter}
        title="Cancel Reservation"
        closeModal={this.closeModalHandler}
      >
        {modalBody}
      </Modal>
    )
  }
}

export default withRouter(CancelReservation);